.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
    
  &__logo {
    display: flex;
    justify-content: center;
    align-items: center;
  }
    
  &__nav-list {
    display: flex;
    flex-direction: row;
    list-style: none;
    padding: 0;
    margin: 0;
    height: 60px;
  }
}
    
.nav-list__item:not(:last-child) {
  margin-right: 20px;
}
    
.item {
  &__link {
    display: flex;
    justify-content: center;
    font-size: 12px;
    text-transform: uppercase;
    color: #060b35;
    text-decoration: none;
    padding: 22.5px 0 23px;
  }
    
  &__link:hover {
    color: #00acdc;
  }
}

.is-active {
  display: flex;
  justify-content: center;
  color: #00acdc;
  font-size: 12px;
  position: relative;
  text-transform: uppercase;
  text-decoration: none;

  &::after {
    content: "";
    background-color: #00acdc;
    position: absolute;
    width: 100%;
    height: 4px;
    left: 0;
    bottom: 0;
    border-radius: 8px;
  }
}
